<script setup lang="ts">
import { useTranslations } from '@/composables/generics/translations';
import { MESSAGE_TIMEOUT } from '@/config/WidgetConfiguration';
import { useAdminConfigStore } from '@/stores/AdminConfigStore';
//import { useSessionStore } from '@/stores/SessionStore';
import { storeToRefs } from 'pinia'
import { ref, computed, onMounted } from 'vue'
import { message } from 'vuekit';

console.log('++++++++++++++++++++++++++ ManageSettings.vue script loaded ++++++++++++++++++++++++++')

const { $i18n } = useTranslations('manage-settings')

const adminConfigStore = useAdminConfigStore()
const { adminConfigData } = storeToRefs(adminConfigStore)

//const sessionStore = useSessionStore()
//const { session } = storeToRefs(sessionStore)
/*watch(session, () => {
  if(session.value.isadmin) {
    adminConfigStore.fetch()
    .then(() => {
      applySettingsToSwitchElements()
    })
    .catch(error => {
      message.create({
        text: $i18n('errorFetch'),
        color: 'error',
        timeout: MESSAGE_TIMEOUT
      })
    })
  }
})*/

onMounted(() => {
  adminConfigStore.fetch()
  .then(() => {
    applySettingsToSwitchElements()
  })
  .catch(error => {
    message.create({
      text: $i18n('errorFetch'),
      color: 'error',
      timeout: MESSAGE_TIMEOUT
    })
  })
  console.log('++++++++++++++++++++++++++ ManageSettings.vue mounted ++++++++++++++++++++++++++')
})

const isAdminConfigDataAvailable = computed(() => adminConfigData.value ? true : false)

const newUsersDeployment = computed(() => $i18n('deployementSection'))
const newUsersDeploymentOptionsModel = ref<string[]>([]);
const newUsersDeploymentOptions = computed( () => ([
    {
        value: 'addUsersToCS',
        label: $i18n('addUsersToCS') + 'Common Space'
    },
    {
        value: 'givecreationright',
        label: $i18n('givecreationright')
    },
    {
        value: 'giveCreationtoXapps',
        label: $i18n('giveCreationtoXapps')
    }
]));

const responsibilityVisibility = computed(() => $i18n('rolesAvailability'))
const responsibilityVisibilityOptionsModel = ref(['', '', '', '']);
const responsibilityVisibilityOptions = computed(() => ([
    {
        value: 'readerRole',
        label: $i18n('reader')
    },
    {
        value: 'publicReaderRole',
        label: $i18n('publicReader')
    },
    {
        value: 'restrictedRoles',
        label: $i18n('restrictedRoles')
    },
    {
        value: 'leaderOwner',
        label: $i18n('associateLeaderOwner')
    }
]));

const manageVisibility = computed(() => $i18n('manageVisibility'))
const manageVisibilityOptionsModel = ref();
const manageVisibilityOptions = computed(() => ([
    {
        value: 'manageVisibility',
        label: $i18n('visibility')
    }
]));

const saveButtonText = computed(() => $i18n('save'))
const cancelButtonText = computed(() => $i18n('cancel'))

function applySettingsToSwitchElements() { 
    newUsersDeploymentOptionsModel.value = [];
    if(adminConfigData.value.addoncommonspace) {
        newUsersDeploymentOptionsModel.value.push('addUsersToCS');
    }
    if(adminConfigData.value.givecreationright) {
        newUsersDeploymentOptionsModel.value.push('givecreationright');
    }
    if(adminConfigData.value.personalspaceavailable) {
        newUsersDeploymentOptionsModel.value.push('giveCreationtoXapps');
    }

    responsibilityVisibilityOptionsModel.value = [];
    if(adminConfigData.value.hasreaderrole) {
        responsibilityVisibilityOptionsModel.value.push('readerRole');
    }
    if(adminConfigData.value.haspublicreaderrole) {
        responsibilityVisibilityOptionsModel.value.push('publicReaderRole');
    }
    if(adminConfigData.value.hasrestrictedroles) {
        responsibilityVisibilityOptionsModel.value.push('restrictedRoles');
    }
    if(adminConfigData.value.mergeprojectroles) {
        responsibilityVisibilityOptionsModel.value.push('leaderOwner');
    }
    
    if(adminConfigData.value.managevisibility) {
        manageVisibilityOptionsModel.value = 'manageVisibility';
    }
 }

function resetSettings() {
    applySettingsToSwitchElements();
}

function updateSettings() {
  let newSettings = {
    addoncommonspace: newUsersDeploymentOptionsModel.value.includes('addUsersToCS'),
    givecreationright: newUsersDeploymentOptionsModel.value.includes('givecreationright'),
    personalspaceavailable: newUsersDeploymentOptionsModel.value.includes('giveCreationtoXapps'),
    hasreaderrole: responsibilityVisibilityOptionsModel.value.includes('readerRole'),
    haspublicreaderrole: responsibilityVisibilityOptionsModel.value.includes('publicReaderRole'),
    hasrestrictedroles: responsibilityVisibilityOptionsModel.value.includes('restrictedRoles'),
    mergeprojectroles: responsibilityVisibilityOptionsModel.value.includes('leaderOwner'),
    managevisibility: manageVisibilityOptionsModel.value ? true : false
  };

  console.log(newSettings);

  adminConfigStore.update(newSettings)
  .then(() => {
      message.create({
          text: $i18n('successSave'),
          color: 'success',
          timeout: MESSAGE_TIMEOUT
      })
  })
  .catch(() => {
      resetSettings();
      message.create({
          text: $i18n('errorSave'),
          color: 'error',
          timeout: MESSAGE_TIMEOUT
      })
  })
}
</script>

<template>
  <vu-scroller>
  <div>
    <div v-show="isAdminConfigDataAvailable">
      <h4 v-text="newUsersDeployment"></h4>
      <hr>
      <vu-checkbox
        type="switch"
        :options="newUsersDeploymentOptions"
        v-model="newUsersDeploymentOptionsModel"
      >
      </vu-checkbox>

      <h4 v-text="responsibilityVisibility"></h4>
      <hr>
      <vu-checkbox
        type="switch"
        :options="responsibilityVisibilityOptions"
        v-model="responsibilityVisibilityOptionsModel"
      >
      </vu-checkbox>

      <h4 v-text="manageVisibility"></h4>
      <hr>
      <vu-checkbox
        type="switch"
        :options="manageVisibilityOptions"
        v-model="manageVisibilityOptionsModel"
      >
      </vu-checkbox>

      <div class="footer">
        <vu-btn
          color="primary"
          class="footer-btn"
          v-text="saveButtonText"
          @click="updateSettings"
        />
        <vu-btn
          class="footer-btn"
          v-text="cancelButtonText"
          @click="resetSettings"
        />
      </div>
    </div>
    <div v-show="!isAdminConfigDataAvailable" class="noAdminConfigData">
      <span v-text="$i18n('errorFetch')"></span>
      <br>
      <vu-icon
        class="fonticon fonticon-error"
      ></vu-icon>
    </div>
  </div>
</vu-scroller>
</template>

<style scoped>
    .noAdminConfigData {
        text-align: center;
    }
    .footer {
        float: right;
    }
    .footer-btn {
        margin-top: 10px;
        margin-left: 10px;
    }
</style>
