<script setup>
import { ref, onMounted, watch } from "vue"
import { usePreferencesStore } from './stores/PreferencesStore'
import { storeToRefs } from "pinia"
import { requirejs } from "./modules/require"
import { message } from "vuekit"
import { useTranslations } from "./composables/generics/translations"
import { MESSAGE_TIMEOUT } from "./config/WidgetConfiguration"
import AppMenu from "./components/AppMenu.vue"
import ManageCollaborativeSpaces from "./components/ManageCollaborativeSpaces.vue"
import ManagePermissions from "./components/ManagePermissions.vue"
import ManageSettings from "./components/ManageSettings.vue"
import ManageMembers from "./components/ManageMembers.vue"
import { useUserStore } from "./stores/UserStore"
import { useManageMembersStore } from "./stores/ManageMembersStore"
import { useSessionStore } from "./stores/SessionStore"
import { useEventBus } from '@vueuse/core'
import { MENU } from "./constants/menuConstants"

const sessionStore = useSessionStore()
const { session } = storeToRefs(sessionStore)

const userStore = useUserStore()
const { user, isUserLoading, isUserLoaded, userLoadingError } = storeToRefs(userStore)

const preferencesStore = usePreferencesStore()
const { selectedPlatform, isPreferencesLoading, isPreferencesLoaded, preferencesLoadingError } = storeToRefs(preferencesStore)

const manageMembersStore = useManageMembersStore()
const { isManageMembersVisible } = storeToRefs(manageMembersStore)

const { $i18n} = useTranslations('w3dspace-control-center')

watch(session, () => {
  if(!session.value.isadmin) {
    message.create({
      text: $i18n('errorAdminRights'),
      color: 'error',
      timeout: MESSAGE_TIMEOUT
    })
  }
})

watch(selectedPlatform, () => {
  sessionStore.fetch()
})

const onSearchBus = useEventBus('onSearch')
const onResetSearchBus = useEventBus('onResetSearch')

onMounted(() => {
  requirejs(['css!DS/UIKIT/UIKIT'])
  userStore.fetch()
  .then(() => preferencesStore.initializeWidgetPreferences())

  const _widget = window.widget
  _widget.addEvent('onSearch', (userQuery) => {
    onSearchBus.emit({
      query: userQuery,
      tab: activeAppMenu.value
    })
  })

  _widget.addEvent('onResetSearch', () => onResetSearchBus.emit())
})

const activeAppMenu = ref(MENU.MANAGE_COLLABORATIVE_SPACES)
function appMenuChanged(menuName) {
  activeAppMenu.value = menuName
}

const collaborativeSpaceModel = ref()

function showManageMembers(collaborativeSpace) {
  collaborativeSpaceModel.value = JSON.parse(JSON.stringify(collaborativeSpace))
  isManageMembersVisible.value = true
}

function closeManageMembers() {
  isManageMembersVisible.value = false
  collaborativeSpaceModel.value = null
}

watch(isManageMembersVisible, () => {
  if(!isManageMembersVisible.value) {
    collaborativeSpaceModel.value = null
  }
})

</script>

<template>

<vu-message-container class="message-container-z-index" />

<vu-modal-container />

<div class="h-full w-full">
  <span v-if="isUserLoading" v-text="$i18n('loading')"></span>
  <span v-else-if="userLoadingError">Failed to load user</span>
  <div v-else-if="isUserLoaded" class="h-full w-full flex">
    <span v-if="isPreferencesLoading" v-text="$i18n('loading')"></span>
    <span v-else-if="preferencesLoadingError" v-text="$i18n('undefinedStorage')"></span>
    <div v-else-if="isPreferencesLoaded" class="h-full w-full flex">
      <div v-if="session?.isadmin" class="h-full w-full">
        <div v-show="!isManageMembersVisible" class="w-full">
          <AppMenu @menuChange="appMenuChanged"/>

            <ManageCollaborativeSpaces v-show="activeAppMenu === MENU.MANAGE_COLLABORATIVE_SPACES" class="main-content-view" @showManageMembers="showManageMembers"/>

            <ManagePermissions v-show="activeAppMenu === MENU.MANAGE_PERMISSIONS" class="main-content-view"/>

            <ManageSettings v-show="activeAppMenu === MENU.MANAGE_SETTINGS" class="main-content-view"/>
        </div>
        <ManageMembers class="w-full" v-if="isManageMembersVisible" v-model="collaborativeSpaceModel"/>
      </div>
      <div v-else>
        <span v-text="$i18n('errorAdminRights')"></span>
      </div>
    </div>
  </div>
</div>

</template>

<style scoped>
.main-content-view {
  height: calc(100% - 40px);
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.message-container-z-index {
  width: calc(100% - 10px);
  z-index: 5000;
}
</style>
