import { ref } from "vue"
import { requirejs } from "@/modules/require"


type i18nRecord = Record<string, string | undefined>

export type i18nFunc = (key: string, values?: Record<string, unknown>) => string

export function useTranslations(fileName: string, path = 'i18n!DS/W3DSpaceControlCenter/assets/nls/') {
    const i18n = ref<i18nRecord>({})
    const promise = new Promise(async (resolve) => {
        try {
            const [result] = await requirejs<[i18nRecord]>([`${path}${fileName}`])
            i18n.value = Object.assign(i18n.value, result)
        }
        catch (error) {
            /*!isRunningInTest() &&*/ console.error(`Error loading nls ${path}${fileName}`)
        }

        resolve(i18n.value)
    })

    const $i18n: i18nFunc = (key: string, values: Record<string, unknown> = {}) => {
        let value = i18n.value[key] || ''
        Object.entries(values)
            .forEach(([k, val]) => {
                value = value.replace(`{${k}}`, val as string)
            })
        
        return value || key
    }

    return {i18n, $i18n, promise}
}
