import { rowsPerPage } from "@/config/WidgetConfiguration"
import { axiosClient } from "@/util/AxiosClient"
import { parseCredentialsResponse } from "@/util/ResponseParser"
import { resolve } from "path"
import { defineStore } from "pinia"
import { ref } from "vue"

export const usePeopleSearchStore = defineStore('peopleSearchStore', () => {
  const PEOPLE_API_ENDPOINT = '/resources/enocsmrest/admin/peoples'

  const FETCH_API_LIMIT = rowsPerPage

  const people = ref()
  
  const credentials = ref(new Map())
  
  const totalPages = ref(0)
  
  const currentPage = ref(0)

  const totalElements = ref(0)
  
  let lastSearchedPageNumber: number

  const query = ref()

  function search(userQuery: string) {
    query.value = userQuery
    return new Promise((resolve, reject) => {
      search_Internal(0)
      .then((response: any) => {
        people.value = response.data.peoples
        totalPages.value = response.data.nbpage
        totalElements.value = response.data.total
        lastSearchedPageNumber = 0
        resolve(response)
      })
      .catch(error => reject(error))
    })
  }

  function search_Internal(page: number) {
    return new Promise((resolve, reject) => {
      axiosClient.get(PEOPLE_API_ENDPOINT, {
        params: {filter: query.value, page: page, limit: FETCH_API_LIMIT}
      })
      .then(response => {
        resolve(response)
      })
      .catch(error => reject(error))
    })
  }

  function next() {
    if(!shouldSearchNext()) {
      return
    }

    let nextPageNumber = lastSearchedPageNumber + 1
    return new Promise((resolve, reject) => {
      search_Internal(nextPageNumber)
      .then((response: any) => {
        people.value = people.value.concat(response.data.peoples)
        lastSearchedPageNumber = nextPageNumber
        currentPage.value = currentPage.value + 1
        resolve(response)
      })
      .catch(error => reject(error))
    })
  }

  function previous() {
    currentPage.value = currentPage.value - 1
  }

  function reset() {
    people.value = undefined
    credentials.value = new Map()
    totalPages.value = 0
    lastSearchedPageNumber = 0
    currentPage.value = 0
    totalElements.value = 0
    query.value = undefined
  }

  function shouldSearchNext() {
    return currentPage.value === lastSearchedPageNumber
  }

  function fetchCredentials(login: string) {
    return new Promise((resolve, reject) => {
    let cachedUserCredentials = credentials.value.get(login)
    if(cachedUserCredentials === undefined) {
        axiosClient.get(PEOPLE_API_ENDPOINT + '/' + login + '/credentials')
        .then(response => {
        const fetchedCredentials = parseCredentialsResponse(response.data)
        credentials.value.set(login, fetchedCredentials)
        resolve(fetchedCredentials)
        })
        .catch(error => reject(error))
    } else {
        resolve(cachedUserCredentials)
    }
    })
  }

  return {people, totalElements, fetchCredentials, search, next, previous, reset}
})
