import { axiosClient } from "@/util/AxiosClient"
import { defineStore } from "pinia"
import { ref } from "vue"

export const useMainOwnerSuggestionStore = defineStore('mainOwnerSuggestionStore', () => {
  const PEOPLE_API_ENDPOINT = '/resources/enocsmrest/admin/peoples'

  const currentMainOwnerSuggestion = ref(null)

  const mainOwnerSuggestions = ref(null)

  function fetch(searchText: any) {
    return new Promise((resolve, reject) => {
      axiosClient.get(PEOPLE_API_ENDPOINT, {
        params: { filter: searchText, limit: 10 }
      })
      .then(response => {
        mainOwnerSuggestions.value = response.data.peoples.map((suggestion: any) => ({
          label: suggestion.name,
          value: suggestion.login,
          src: suggestion.picture.substring(suggestion.picture.lastIndexOf('https://'))
        }))
        resolve(mainOwnerSuggestions.value)
      })
      .catch(error => reject(error))
    })
  }

  function fetchCurrentMainOwnerSuggestion(searchText: any) {
    return new Promise((resolve, reject) => {
      axiosClient.get(PEOPLE_API_ENDPOINT, {
        params: { filter: searchText, limit: 10 }
      })
      .then(response => {
        currentMainOwnerSuggestion.value = response.data.peoples.map((suggestion: any) => ({
          label: suggestion.name,
          value: suggestion.login,
          src: suggestion.picture.substring(suggestion.picture.lastIndexOf('https://'))
        }))
        resolve(currentMainOwnerSuggestion.value)
      })
      .catch(error => reject(error))
    })
  }

  return { currentMainOwnerSuggestion, mainOwnerSuggestions, fetch, fetchCurrentMainOwnerSuggestion }
})
