import { ref } from 'vue'
import { defineStore } from 'pinia'
import { axiosClient } from '@/util/AxiosClient'
import { parseCredentialsResponse } from '@/util/ResponseParser'
import { rowsPerPage } from '@/config/WidgetConfiguration'

export const usePeopleStore = defineStore('peopleStore', () => {

  const PEOPLE_API_ENDPOINT = '/resources/enocsmrest/admin/peoples'

  const FETCH_API_LIMIT = rowsPerPage

  const people = ref()

  const credentials = ref(new Map())

  const totalPages = ref(0)

  const totalElements = ref(0)

  const currentPage = ref(0)

  let lastFetchedPageNumber: number

  function fetch() {
    fetch_Internal(0)
    .then((response: any) => {
      people.value = response.data.peoples
      totalPages.value = response.data.nbpage
      totalElements.value = response.data.total
      lastFetchedPageNumber = 0
    })
  }

  function fetch_Internal(page: number) {
    return new Promise((resolve, reject) => {
      axiosClient.get(PEOPLE_API_ENDPOINT, {
        params: {page: page, limit: FETCH_API_LIMIT}
      })
      .then(response => {
        resolve(response)
      })
      .catch(error => reject(error))
    })
  }

  function next() {
    if(!shouldFetchNext()) {
      return
    }

    let nextPageNumber = lastFetchedPageNumber + 1
    fetch_Internal(nextPageNumber)
    .then((response: any) => {
      people.value = people.value.concat(response.data.peoples)
      lastFetchedPageNumber = nextPageNumber
      currentPage.value = currentPage.value + 1
    })
  }

  function previous() {
    currentPage.value = currentPage.value - 1
  }

  function resetCurrentPage() {
    currentPage.value = 0
  }

  function shouldFetchNext() {
    return currentPage.value === lastFetchedPageNumber
  }

  function fetchCredentials(login: string) {
    return new Promise((resolve, reject) => {
      let cachedUserCredentials = credentials.value.get(login)
      if(cachedUserCredentials === undefined) {
        axiosClient.get(PEOPLE_API_ENDPOINT + '/' + login + '/credentials')
        .then(response => {
          const fetchedCredentials = parseCredentialsResponse(response.data)
          credentials.value.set(login, fetchedCredentials)
          resolve(fetchedCredentials)
        })
        .catch(error => reject(error))
      } else {
        resolve(cachedUserCredentials)
      }
    })
  }

  function update(requestBody: any) {
    return new Promise((resolve, reject) => {
      axiosClient.put(PEOPLE_API_ENDPOINT + '/' + requestBody.login, requestBody)
      .then(response => {
        people.value.find((person: any) => {
          if(requestBody.login === person.login) {
            person.createcs = response.data.createcs
          }
        })
        resolve(response.data)
      })
      .catch(error => reject(error))
    })
  }

  return { people, totalElements, fetch, next, previous, resetCurrentPage, update, fetchCredentials }
})
