import { axiosClient } from "@/util/AxiosClient"
import { formatString } from "@/util/StringFormatter"

const MEMBERS_API_ENDPOINT = '/resources/enocsmrest/admin/collabspaces/{0}/allmembers/batch'

export function create(roles) {
  return new Promise((resolve, reject) => {
    const membersApiEndpointWithCSName = formatString(MEMBERS_API_ENDPOINT, roles[0].collabspace)
    axiosClient.post(membersApiEndpointWithCSName, roles)
    .then(response => {
      resolve(response.data)
    })
    .catch(error => reject(error))
  })
}
