import { requirejs } from "@/modules/require"
import { I3DXCompassPlatformServices_AMD, I3DXCompassPlatformServicesUser } from "@/typings/i3DXCompassPlatformServices"
import { defineStore } from "pinia"
import { ref } from "vue"

export const useUserStore = defineStore('userStore', () => {
  const user = ref<I3DXCompassPlatformServicesUser>()

  const isUserLoading = ref<boolean>(false)

  const isUserLoaded = ref<boolean>(false)

  const userLoadingError = ref<string | null>(null)

  async function fetch() {
    isUserLoading.value = true
    isUserLoaded.value = false
    userLoadingError.value = null

    const [i3DXCompassPlatformServices] = await requirejs<[I3DXCompassPlatformServices_AMD]>(['DS/i3DXCompassPlatformServices/i3DXCompassPlatformServices'])

    return new Promise((resolve, reject) => {
      i3DXCompassPlatformServices.getUser({
        onComplete: function(response) {
          user.value = response
          isUserLoading.value = false
          isUserLoaded.value = true
          resolve(response)
        },
        onFailure: function (error: any) {
          isUserLoading.value = false
          userLoadingError.value = error.message || 'Failed to fetch user'
          reject(error)
        }
      })
    })
  }

  return {user, isUserLoading, isUserLoaded, userLoadingError, fetch}
})
