// Credits: Swym UI team
import { DS_WEBAPPS_URL } from '@/config/WidgetConfiguration'

/** Hold the promise for require init. */
let initAsync: Promise<unknown> | undefined

async function initRequire() {
  if (initAsync)
    return

  if (window.require && window.widget) {
    window.require.config({
      baseUrl: DS_WEBAPPS_URL,
    })
  }
}

// export default requirejs
/**
 * To fetch modules.
 * @param modules - modules to be fetched
 * @param useTopWindow - True if window.top is to be used. Particularly useful to require modules from dashboard when Swym is in widget mode.
 */
export async function requirejs<T>(modules: string[], useTopWindow = false): Promise<T> {
  if (!initAsync)
    initAsync = initRequire()

  await initAsync

  return new Promise((resolve, reject) => {
    (useTopWindow && window.top ? window.top : window).require(
      modules,
      (...m: any[]) => resolve(m as T),
      reject,
    )
  })
}
