<script setup lang="ts">
import { useTranslations } from '@/composables/generics/translations';
import { useMainOwnerSuggestionStore } from '@/stores/MainOwnerSuggestionStore';
import { storeToRefs } from 'pinia';
import { computed, ref } from 'vue'

const emit = defineEmits<{
    userSelected: [login: string]
}>()

const props = defineProps<{
    currentMainOwnerName: string
}>()

const { $i18n } = useTranslations('manage-collaborative-spaces')

const mainOwnerSuggestionStore = useMainOwnerSuggestionStore()
const { mainOwnerSuggestions } = storeToRefs(mainOwnerSuggestionStore)

const mainOwner = ref()
const mainOwnerPlaceholderText = computed(() => props.currentMainOwnerName + ' ' + $i18n('mainOwnerPlaceholderText'))


function userSelected() {
    emit('userSelected', mainOwner.value[0]?.value ? mainOwner.value[0].value : '');
}

</script>
<template>
  <vu-multiple-select
    v-model="mainOwner"
    user
    :placeholder="mainOwnerPlaceholderText"
    :max-selectable="1"
    :options="mainOwnerSuggestions"
    @search="mainOwnerSuggestionStore.fetch"
    @update:model-value="userSelected"
  />
</template>
<style lang="css" scoped>
    :deep(.select input) {
      width: 100% !important;
    }
</style>
