<script setup lang="ts">
import { computed, onMounted, onUnmounted, ref } from 'vue'
import { useTranslations } from '@/composables/generics/translations'
import { storeToRefs } from 'pinia'
import { usePeopleStore } from '@/stores/PeopleStore'
import { message } from 'vuekit'
import { MESSAGE_TIMEOUT, rowsPerPage } from '@/config/WidgetConfiguration'
import CredentialsModal from './CredentialsModal.vue'
import { usePeopleSearchStore } from '@/stores/PeopleSearchStore'
import { MENU } from '@/constants/menuConstants'
import { useEventBus } from '@vueuse/core'
import EmptyView from './EmptyView.vue'

const { $i18n } = useTranslations('manage-permissions')

const peopleStore = usePeopleStore()
const { people: fetchedPeople, totalElements: fetchedPeopleTotalElements } = storeToRefs(peopleStore)

const peopleSearchStore = usePeopleSearchStore()
const { people: searchedPeople, totalElements: searchedPeopleTotalElements } = storeToRefs(peopleSearchStore)

const isSearchActive = ref(false)

const gridViewHeaders = computed(() => {
  return [
    {
      property: 'picture',
      sortable: false
    },
    {
      property: 'name',
      label: $i18n('name'),
      sortable: false
    },
    {
      property: 'createCSAllowed',
      label: $i18n('createCSAllowed'),
      sortable: false
    },
    {
      property: 'email',
      label: $i18n('email'),
      sortable: false
    },
    {
      property: 'admin',
      label: $i18n('admin'),
      sortable: false
    },
    {
      property: 'actions',
      sortable: false
    }
  ]
})

const fetchedPeopleGridViewItems = computed(() => {
  return fetchedPeople.value?.map((person: any) => {
    return {
      picture: person.picture.split('redirect=')[1],
      name: person.name,
      createCSAllowed: person.createcs ? $i18n('yes') : $i18n('no'),
      email: person.email,
      admin: person.isadmin ? $i18n('yes') : $i18n('no'),
      actions: computePersonActions(person)
    }
  })
})

const gridViewItems = ref()

const totalElements = ref()

const credentialsModalVisible = ref(false)

function computePersonActions(person: any) {
  const actions = []
  actions.push({
    text: $i18n('displayCredentials'),
    fonticon: 'info',
    handler: () => displayCredentialsModal(person.login)
  })

  if(person.createcs) {
    if(!person.isadmin) {
      actions.push({
        text: $i18n('denyCreation'),
        fonticon: 'fonticon fonticon-block',
        handler: () => handleDenyCreation(person)
      })
    }
  } else {
    actions.push({
      text: $i18n('allowCreation'),
      fonticon: 'pencil',
      handler: () => handleAllowCreation(person)
    })
  }

  return actions
}

const credentialsModel = ref()

function closeCredentialsModal() {
  credentialsModalVisible.value = false
  credentialsModel.value = undefined
}

function displayCredentialsModal(login: any) {
  peopleStore.fetchCredentials(login)
  .then(response => {
    credentialsModel.value = response
    credentialsModalVisible.value = true
  })
  .catch(error => console.log(error))
}

function handleAllowCreation(person: any) {
  const requestBody = Object.assign({}, person)
  requestBody.createcs = true
  peopleStore.update(requestBody)
  .then(() => {
    message.create({
      text: $i18n('allowCreationSuccess'),
      color: 'success',
      timeout: MESSAGE_TIMEOUT
    })
  })
  .catch(error => {
    message.create({
      text: $i18n('allowCreationError'),
      color: 'error',
      timeout: MESSAGE_TIMEOUT
    })
  })
}

function handleDenyCreation(person: any) {
  const requestBody = Object.assign({}, person)
  requestBody.createcs = false
  peopleStore.update(requestBody)
  .then(() => {
    message.create({
      text: $i18n('denyCreationSuccess'),
      color: 'success',
      timeout: MESSAGE_TIMEOUT
    })
  })
  .catch(error => {
    message.create({
      text: $i18n('denyCreationError'),
      color: 'error',
      timeout: MESSAGE_TIMEOUT
    })
  })
}

function onPageDown() {
  if(isSearchActive.value) {
    peopleSearchStore.previous()
  } else {
    peopleStore.previous()
  }
}

function onPageUp() {
  if(isSearchActive.value) {
    peopleSearchStore.next()
  } else {
    peopleStore.next()
  }
}

const searchedPeopleGridViewItems = computed(() => {
  return searchedPeople.value?.map((person: any) => {
    return {
      picture: person.picture.split('redirect=')[1],
      name: person.name,
      createCSAllowed: person.createcs ? $i18n('yes') : $i18n('no'),
      email: person.email,
      admin: person.isadmin ? $i18n('yes') : $i18n('no'),
      actions: computePersonActions(person)
    }
  })
})

function handleOnSearch(options: any) {
  if(options.tab === MENU.MANAGE_PERMISSIONS) {
    isSearchActive.value = true
    peopleSearchStore.search(options.query)
    .then(() => {
      gridViewItems.value = searchedPeopleGridViewItems
      totalElements.value = searchedPeopleTotalElements
    })
  }
}

function handleOnResetSearch() {
  if(isSearchActive.value) {
    isSearchActive.value = false
    gridViewItems.value = fetchedPeopleGridViewItems
    totalElements.value = fetchedPeopleTotalElements
    peopleStore.resetCurrentPage()
    peopleSearchStore.reset()
  }
}

const onSearchBus = useEventBus('onSearch')
const onResetSearchBus = useEventBus('onResetSearch')

onMounted(() => {
  peopleStore.fetch()
  gridViewItems.value = fetchedPeopleGridViewItems
  totalElements.value = fetchedPeopleTotalElements

  onSearchBus.on(handleOnSearch)
  onResetSearchBus.on(handleOnResetSearch)
})

onUnmounted(() => {
  onSearchBus.off(handleOnSearch)
  onResetSearchBus.off(handleOnResetSearch)
})

</script>

<template>
  <div>
    <EmptyView v-if="isSearchActive && (gridViewItems.value?.length === 0)" />
    <vu-scroller>
      <vu-grid-view
        :headers="gridViewHeaders"
        :items="gridViewItems.value"
        :rowsPerPage.sync="rowsPerPage"
        :serverItemsLength="totalElements.value"
        :hideRowsPerPageSelect="true"
        @pageDown="onPageDown"
        @pageUp="onPageUp">
        <template v-slot:picture="item">
          <vu-user-picture  size="small" :src="item.picture"/>
        </template>
        <template v-slot:actions="item">
          <vu-dropdownmenu responsive="true" :items="item.actions">
            <vu-icon-btn class="chevron-menu-icon" icon="chevron-down"/>
          </vu-dropdownmenu>
        </template>
      </vu-grid-view>
    </vu-scroller>
    <CredentialsModal :show="credentialsModalVisible" v-model="credentialsModel" @close="closeCredentialsModal"></CredentialsModal>
  </div>
</template>

<style scoped>
  ::v-deep td {
    vertical-align: middle;
  }
</style>
