<script setup lang="ts">
import { useTranslations } from '@/composables/generics/translations'
import { computed, ref } from 'vue'
import { ModalMode } from './model/ModalMode'
import { useMainOwnerSuggestionStore } from '@/stores/MainOwnerSuggestionStore';
import { storeToRefs } from 'pinia';
import { getDateAndTime } from '@/util/DateFormatter';
import { message } from 'vuekit';
import { MESSAGE_TIMEOUT } from '@/config/WidgetConfiguration';
import { useCollaborativeSpaceStore } from '@/stores/CollaborativeSpaceStore';
import { CollaborativeSpace } from './model/CollaborativeSpace';
import { useUserStore } from '@/stores/UserStore';

defineProps<{
  show: boolean
}>()

const emit = defineEmits<{
  close: []
}>()

const userStore = useUserStore()
const { user } = storeToRefs(userStore)

const collaborativeSpaceStore = useCollaborativeSpaceStore()

const MAX_TITLE_LENGTH = 32;
const MAX_DESCRIPTION_LENGTH = 255;

const model = defineModel<CollaborativeSpace>()
const modalMode = defineModel('modalMode');
const { $i18n } = useTranslations('manage-collaborative-spaces')

const isCreateMode = computed(() => modalMode.value === ModalMode.CREATE)
const isEditMode = computed(() => modalMode.value === ModalMode.EDIT)
const isPropertiesMode = computed(() => modalMode.value === ModalMode.PROPERTIES)

const isEditTitleAllowed = computed(() => {
    const actions = model.value?.actions ? model.value.actions : []
    for (let actionIndex = 0; actionIndex < actions.length; actionIndex++) {
        const action = actions[actionIndex]
        if(action === 'edit.title') {
            return true
        }
    }
    return false
})
const isEditDescriptionAllowed = computed(() => {
    const actions = model.value?.actions ? model.value.actions : []
    for (let actionIndex = 0; actionIndex < actions.length; actionIndex++) {
        const action = actions[actionIndex]
        if(action === 'edit.description') {
            return true
        }
    }
    return false
})
const isEditVisibilityAllowed = computed(() => {
    const actions = model.value?.actions ? model.value.actions : []
    for (let actionIndex = 0; actionIndex < actions.length; actionIndex++) {
        const action = actions[actionIndex]
        if(action === 'edit.visibility') {
            return true
        }
    }
    return false
})
const isGlobalEditAllowed = computed(() => isEditTitleAllowed.value && isEditDescriptionAllowed.value && isEditVisibilityAllowed.value)
const editPropertiesTooltip = computed(() => $i18n('editProperties') === 'editProperties' ? 'Edit properties' : $i18n('editProperties'))
const modalTitle = computed(() => isCreateMode ? $i18n('createCollabSpace') : $i18n('properties'))

const title = ref('')
const isTitleChanged = computed(() => {
    const newTitle = title.value.trim()
    return newTitle.length !== 0 && newTitle !== model.value?.title
})

const description = ref('')
const isDescriptionChanged = computed(() => {
    const newDescription = description.value.trim()
    return newDescription !== model.value?.description
})

const mainOwnerPlaceholderText = computed(() => user.value?.firstname + ' ' + user.value?.lastname + ' ' + $i18n('mainOwnerPlaceholderText'))
const newMainOwner = ref()
const mainOwnerSuggestionStore = useMainOwnerSuggestionStore()
const { mainOwnerSuggestions } = storeToRefs(mainOwnerSuggestionStore)

const selectedFamily = ref('DesignTeam')
const selectedVisibility = ref('Private')
const isVisibilityChanged = computed(() => selectedVisibility.value !== model.value?.visibility.value)

const familyOptions = computed(() => {
  return [
    {
      value: 'DesignTeam',
      label: $i18n('design') + ' - ' + $i18n('designDetail')
    },
    {
      value: 'StandardTeam',
      label: $i18n('standard') + ' - ' + $i18n('standardDetail')
    }
  ]
})

const visibilityOptions = computed(() => {
  return [
    {
      value: 'Private',
      label: $i18n('private') + ' - ' + $i18n('privateDetail'),
      disabled: selectedFamily.value === 'StandardTeam' ? true : false
    },
    {
      value: 'Protected',
      label: $i18n('protected') + ' - ' + $i18n('protectedDetail'),
      disabled: selectedFamily.value === 'StandardTeam' ? true : false
    },
    {
      value: 'Public',
      label: $i18n('public') + ' - ' + $i18n('publicDetail')
    }
  ]
})

const showVisibilityChangeConfirmation = ref(false)

function closeVisibilityChangeConfirmationModal() {
    showVisibilityChangeConfirmation.value = false
}

function emitClose() {
  reset()
  emit('close')
}

function familyChanged() {
  if(selectedFamily.value === 'StandardTeam') {
    selectedVisibility.value = 'Public';
  }
}

function  getVisibilityMessageNLS() {
  const oldVisibility = model.value?.visibility.value || ''
  const newVisibility = selectedVisibility.value
  const title = model.value?.title
  const orderedVisibilities = ['PRIVATE', 'PROTECTED', 'PUBLIC']
  const oldOrder = orderedVisibilities.findIndex((element) => element === oldVisibility.toUpperCase())
  const newOrder = orderedVisibilities.findIndex((element) => element === newVisibility.toUpperCase())
  const message = []

  if(newOrder > oldOrder) {
    message.push($i18n('visibilityIncreaseConfirmMessage1', {name: title}))
    message.push($i18n('visibilityIncreaseConfirmMessage2'))
  } else if (newOrder < oldOrder) {
    message.push($i18n('visibilityReduceConfirmMessage1', {name: title}))
    message.push($i18n('visibilityReduceConfirmMessage2'))
  }
  return message
}



function handleCreateCollaborativeSpace() {
  if(!validateTitle()) {
    return
  }
  
  const newCS = {
    title: title.value,
    description: description.value,
    family: {
        value: selectedFamily.value
    },
    visibility: {
        value: selectedVisibility.value
    },
    owner: {
        login: Array.isArray(newMainOwner.value) && newMainOwner.value[0] ? newMainOwner.value[0].value : user.value?.id
    }
  }
  console.log('-----------------------------------newCS: ' + JSON.stringify(newCS))
  collaborativeSpaceStore.create(newCS)
  .then((response: any) => {
    message.create({
      text: $i18n('createCSSuccess', {collaborativeSpaceName: response.title}),
      color: 'success',
      timeout: MESSAGE_TIMEOUT
    })
  })
  .catch(error => {
    message.create({
      text: $i18n('createCSError', {collaborativeSpaceName: newCS.title}),
      color: 'error',
      timeout: MESSAGE_TIMEOUT
    })
    console.error(error)
  })
  emitClose()
}



function handleUpdateCollaborativeSpace() {
  if(!validateTitle()) {
    return
  }

  if(isVisibilityChanged.value) {
    showVisibilityChangeConfirmation.value = true
  } else {
    updateCollaborativeSpace()
  }
}

function reset() {
  title.value = ''
  description.value = ''
  if(isCreateMode) {
    newMainOwner.value = []
    mainOwnerSuggestions.value = null//[];
    selectedFamily.value = 'DesignTeam'
    selectedVisibility.value = 'Private'
  } else if(isEditMode) {
    selectedVisibility.value = ''
  }
}

function switchToEditMode() {
    reset()
    modalMode.value = ModalMode.EDIT
    title.value = model.value.title //|| '';
    description.value = model.value.description //|| '';
    selectedFamily.value = model.value.family.value
    selectedVisibility.value = model.value.visibility.value //|| '';
}

function switchToPropertiesMode() {
  reset()
  modalMode.value = ModalMode.PROPERTIES
}

function updateCollaborativeSpace() {
  const collaborativeSpace = {
    name: model.value.name,
    title: isTitleChanged ? title.value : model.value.title,
    thumbnail: model.value.thumbnail,
    description: isDescriptionChanged ? description.value : model.value.description,
    family: {
      name: model.value.family.name,
      value: model.value.family.value
    },
    visibility: {
      value: selectedVisibility.value.toUpperCase() 
    },
    owner: {
      createcs: model.value.owner.createcs,
      employment: model.value.owner.employment,
      fname: model.value.owner.fname,
      lname: model.value.owner.lname,
      login: model.value.owner.login,
      name: model.value.owner.name,
      picture: model.value.owner.picture
    },
    solution: model.value.solution,
    actions: model.value.actions.map((action: any) => action),
    contexts: model.value.contexts.map((context: any) => context),
    created: model.value.created,
    modified: model.value.modified
  }

  collaborativeSpaceStore.update(collaborativeSpace)
  .then(updatedCollaborativeSpace => {
    emitClose()
    switchToPropertiesMode()
    message.create({
      text: $i18n('updateCollaborativeSpaceSuccess'),
      color: 'success',
      timeout: MESSAGE_TIMEOUT
    })
  })
  .catch(error => {
    message.create({
      text: $i18n('updateCollaborativeSpaceError'),
      color: 'error',
      timeout: MESSAGE_TIMEOUT
    })
  })
}

function validateTitle() {
    if(title.value === undefined || title.value.length === 0 || title.value.trim() === '') {
        message.create({
            text: $i18n('titleCannotBeEmptyError'),
            color: 'error',
            timeout: MESSAGE_TIMEOUT
        })

        return false
    }

    // if the title value contains more than one space chars
    if(title.value.includes('  ')) {
        message.create({
            text: $i18n('titleWhiteSpaceError'),
            color: 'error',
            timeout: MESSAGE_TIMEOUT
        })

        return false
    }

    // if the title value contains forbidden chars
    const pattern = new RegExp ('^((?!<)(?!>)(?!/)(?!\\\\)(?!:)(?!\\?)(?!\\*)(?!")(?!\\|)(?!@)(?!#)(?!\\$)(?!%)(?!,)(?!\\.)(?!\\;)(?!\\[)(?!\\]).)*$')
    if(!pattern.test(title.value)) {
        const forbiddenchars = " \\ \" # $ @ % * , ; ? < > [ ] | : . '"
        message.create({
            text: $i18n('titleCannotContainForbiddenCharsError', {forbiddenCharList: forbiddenchars}),
            color: 'error',
            timeout: MESSAGE_TIMEOUT
        })

        return false
    }

    return true
}

function visibilityChangedConfirmed() {
    showVisibilityChangeConfirmation.value = false;
    updateCollaborativeSpace();
}
</script>

<template>
  <vu-modal :show="show" :title="modalTitle" :noMobile="true" :keyboard="false" @close="emitClose">

    <template #modal-body>
      <div class="properties-table">
        <div class="properties-table-row">
          <vu-image class="thumbnail-cell" v-show="!isCreateMode" :src="model?.thumbnail"/>
          <h3 class="property-value" v-show="!isCreateMode" v-text="model?.title"></h3>
          <vu-icon class="fonticon-pencil edit-properties-fonticon" v-show="isPropertiesMode && isGlobalEditAllowed" :title="editPropertiesTooltip" @click="switchToEditMode"/>
        </div>
      </div>
      <div class="properties-table properties-table-extras">
        <div class="properties-table-row">
          <div class="property-name">
            <span v-text="$i18n('title')"></span>
            <span class="property-required" v-text="$i18n('fieldRequired')" v-show="isCreateMode || (isEditMode && isEditTitleAllowed)"></span>
          </div>
          <div class="property-value">
            <span v-text="model?.title" v-show="isPropertiesMode || (isEditMode && !isEditTitleAllowed)"></span>
            <!-- Need enter key working on vu-input -->
            <vu-input
              v-show="isCreateMode || ( isEditMode && isEditTitleAllowed )"
              v-model="title"
              :placeholder="$i18n('titlePlaceholderText')"
              :maxlength="MAX_TITLE_LENGTH"
              @keyup.enter="handleCreateCollaborativeSpace"
            />
          </div>
        </div>
        <div class="properties-table-row" v-show="!isCreateMode">
          <span class="property-name" v-text="$i18n('name')"></span>
          <span class="property-value" v-text="model?.name"></span>
        </div>
        <div class="properties-table-row">
          <span class="property-name" v-text="$i18n('description')"></span>
          <div class="property-value">
            <span v-text="model?.description" v-show="isPropertiesMode || (isEditMode && !isEditDescriptionAllowed)"></span>
            <vu-textarea
              v-show="isCreateMode || (isEditMode && isEditDescriptionAllowed)"
              v-model="description"
              :placeholder="$i18n('descriptionPlaceholderText')"
              :maxlength="MAX_DESCRIPTION_LENGTH"
            />
          </div>
        </div>
        <div class="properties-table-row">
          <span class="property-name" v-text="$i18n('mainOwner')"></span>
          <div class="property-value">
            <span v-text="model?.owner.name" v-show="!isCreateMode"></span>
            <vu-multiple-select
              v-show="isCreateMode"
              v-model="newMainOwner"
              :placeholder=mainOwnerPlaceholderText
              :max-selectable="1"
              :options="mainOwnerSuggestions"
              @search="mainOwnerSuggestionStore.fetch"
            />
          </div>
        </div>
        <div class="properties-table-row">
          <span class="property-name" v-text="$i18n('family')"></span>
          <div class="property-value">
            <span v-text="model?.family.name" v-show="!isCreateMode"></span>
            <vu-select
              v-show="isCreateMode"
              hide-placeholder-option
              v-model="selectedFamily"
              :options="familyOptions"
              @update:model-value="familyChanged"
            />
          </div>
        </div>
        <div class="properties-table-row">
          <span class="property-name" v-text="$i18n('visibility')"></span>
          <div class="property-value">
            <span v-text="model?.visibility.name" v-show="isPropertiesMode || (isEditMode && !isEditVisibilityAllowed)"></span>
            <vu-select
              v-show="isCreateMode || (isEditMode && isEditVisibilityAllowed)"
              hide-placeholder-option
              v-model="selectedVisibility"
              :options = "visibilityOptions"
            />
          </div>
        </div>
        <div class="properties-table-row" v-show="isPropertiesMode || isEditMode">
          <span class="property-name" v-text="$i18n('modificationDate')"></span>
          <span class="property-value" v-text="getDateAndTime(model?.modified)"></span>
        </div>
        <div class="properties-table-row" v-show="isPropertiesMode || isEditMode">
          <span class="property-name" v-text="$i18n('creationDate')"></span>
          <span class="property-value" v-text="getDateAndTime(model?.created)"></span>
      </div>
      </div>
    </template>

    <template #modal-footer>
      <vu-btn color="primary" v-show="isCreateMode" v-text="$i18n('create')" @click="handleCreateCollaborativeSpace"/>
      <vu-btn v-show="isCreateMode" v-text="$i18n('cancel')" @click="emitClose"/>
      <vu-btn v-show="isEditMode" v-text="$i18n('edit')" color="primary" @click="handleUpdateCollaborativeSpace" :disabled="!(isTitleChanged || isDescriptionChanged || isVisibilityChanged)"/>
      <vu-btn v-show="isEditMode" v-text="$i18n('cancel')" @click="switchToPropertiesMode"/>
      <vu-btn v-show="isPropertiesMode" v-text="$i18n('close')" @click="emitClose"/>
    </template>

  </vu-modal>
  <vu-modal :title="$i18n('visibilityConfirmTitle')" :show="showVisibilityChangeConfirmation" :noMobile="true">
    <template #modal-body>
      <span v-text="getVisibilityMessageNLS()[0]"></span>
      <br><br>
      <b><span v-text="getVisibilityMessageNLS()[1]"></span></b>
    </template>
    <template #modal-footer>
      <vu-btn
        v-text="$i18n('confirm')"
        color="warning"
        @click="visibilityChangedConfirmed"
      />
      <vu-btn
        v-text="$i18n('cancel')"
        @click="closeVisibilityChangeConfirmationModal"
      />
    </template>
  </vu-modal>
</template>
<style scoped>
:deep(.modal-content) {
  width: 710px;
}
.properties-table {
    display: table;
}
.properties-table-extras {
    line-height: 45px;
    padding-left: 30px;
}
.properties-table-row {
    display: table-row;
}
.property-name {
    display: table-cell;
    white-space: nowrap;
    color: #000000;
}
.property-name::after {
    content: ":";
}
.property-value {
    display: table-cell;
    vertical-align: middle;
    padding-left: 30px;
    width: 100%;
}
.property-required {
    color: #ff0000;
}
.thumbnail-cell {
    width: 120px;
    height: 90px;
}
.edit-properties-fonticon {
    display: table-cell;
    cursor: pointer;
    vertical-align: middle;
}
</style>
