<script setup lang="ts">
import { useTranslations } from '@/composables/generics/translations'
import { useCollaborativeSpaceStore } from '@/stores/CollaborativeSpaceStore'
import { getDate } from '@/util/DateFormatter'
import { storeToRefs } from 'pinia'
import { computed, onMounted, onUnmounted, ref } from 'vue'
import { ModalMode } from './model/ModalMode'
import { CollaborativeSpace } from './model/CollaborativeSpace'
import CollaborativeSpaceModal from './CollaborativeSpaceModal.vue'
import ChangeMainOwnerModal from './ChangeMainOwnerModal.vue'
import { rowsPerPage } from '@/config/WidgetConfiguration'
import { useEventBus } from '@vueuse/core'
import { useCollaborativeSpaceSearchStore } from '@/stores/CollaborativeSpaceSearchStore'
import { MENU } from '@/constants/menuConstants'
import EmptyView from './EmptyView.vue'

const emit = defineEmits<{
  showManageMembers: [CollaborativeSpace]
}>()

const { $i18n } = useTranslations('manage-collaborative-spaces')

const collaborativeSpaceStore = useCollaborativeSpaceStore()
const { collaborativeSpaces: fetchedCollaborativeSpaces, serverItemsLength: fetchedCSServerItemsLength } = storeToRefs(collaborativeSpaceStore)

const collaborativeSpaceSearchStore = useCollaborativeSpaceSearchStore()
const { collaborativeSpaces: searchedCollaborativeSpaces, serverItemsLength: searchedCSServerItemsLength } = storeToRefs(collaborativeSpaceSearchStore)

const isSearchActive = ref(false)

const createCollabSpaceButtonText = computed(() => $i18n('createCollabSpace'))
const headers = computed(() => {
  return [
  {
      property: 'title',
      label: $i18n('title'),
      sortable: true
    },
    {
      property: 'name',
      label: $i18n('name'),
      sortable: false
    },
    {
      property: 'visibility',
      label: $i18n('visibility'),
      sortable: false
    },
    {
      property: 'mainOwner',
      label: $i18n('mainOwner'),
      sortable: false
    },
    {
      property: 'family',
      label: $i18n('family'),
      sortable: false
    },
    {
      property: 'creationDate',
      label: $i18n('creationDate'),
      sortable: false
    },
    {
      property: 'description',
      label: $i18n('description'),
      sortable: false
    },
    {
      property: 'actions',
      sortable: false
    }
  ]
})

const fetchedCollaborativeSpaceGridViewItems = computed(() => {
  return fetchedCollaborativeSpaces.value?.map((collaborativeSpace: CollaborativeSpace) => {
    return {
      title: collaborativeSpace.title,
      name: collaborativeSpace.name,
      visibility: collaborativeSpace.visibility.value,
      mainOwner: collaborativeSpace.owner.name,
      family: collaborativeSpace.family.name,
      creationDate: getDate(collaborativeSpace.created),
      description: collaborativeSpace.description,
      actions: computeActions(collaborativeSpace)
    }
  })
})

const gridViewItems = ref()

const serverItemsLength = ref()

const collaborativeSpaceModalMode = ref(ModalMode.PROPERTIES)
const collaborativeSpaceModel = ref()

const showCollaborativeSpaceModal = ref(false)
const isChangeOwnerModalVisible = ref(false)

function onPageDown() {
  if(isSearchActive.value) {
    collaborativeSpaceSearchStore.previous()
  } else {
    collaborativeSpaceStore.previous()
  }
}

function onPageUp() {
  if(isSearchActive.value) {
    collaborativeSpaceSearchStore.next()
  } else {
    collaborativeSpaceStore.next()
  }
}

function computeActions(collaborativeSpace: CollaborativeSpace) {
  const actions = [
  {
      text: $i18n('viewProperties'),
      fonticon: 'info',
      handler: () => showCollaboraiveSpaceProperties(collaborativeSpace)
    },
    {
      text: $i18n('changeOwner'),
      fonticon: 'fonticon fonticon-user-key',
      handler: () => showChangeMainOwnerModal(collaborativeSpace)
    },
    {
      text: $i18n('manageMembers'),
      fonticon: 'fonticon fonticon-user-cog fonticon-clickable',
      handler: () => showManageMembers(collaborativeSpace)
    }
  ]

  return actions
}

function closeCollaborativeSpaceModal() {
  showCollaborativeSpaceModal.value = false
  collaborativeSpaceModel.value = null
}

function closeChangeMainOwnerModal() {
  isChangeOwnerModalVisible.value = false
  collaborativeSpaceModel.value = null
}

function showChangeMainOwnerModal(collaborativeSpace: CollaborativeSpace) {
  collaborativeSpaceModel.value = collaborativeSpace
  isChangeOwnerModalVisible.value = true
}

function showCollaborativeSpaceCreateModal() {
    collaborativeSpaceModalMode.value = ModalMode.CREATE;
    showCollaborativeSpaceModal.value = true;
}

function showCollaboraiveSpaceProperties(collaborativeSpace: CollaborativeSpace) {
  collaborativeSpaceModel.value = collaborativeSpace
  collaborativeSpaceModalMode.value = ModalMode.PROPERTIES
  showCollaborativeSpaceModal.value = true
}

function showManageMembers(collaborativeSpace: CollaborativeSpace) {
  emit('showManageMembers', collaborativeSpace)
}

const searchedCollaborativeSpaceGridViewItems = computed(() => {
  return searchedCollaborativeSpaces.value?.map((collaborativeSpace: CollaborativeSpace) => {
    return {
      title: collaborativeSpace.title,
      name: collaborativeSpace.name,
      visibility: collaborativeSpace.visibility.value,
      mainOwner: collaborativeSpace.owner.name,
      family: collaborativeSpace.family.name,
      creationDate: getDate(collaborativeSpace.created),
      description: collaborativeSpace.description,
      actions: computeActions(collaborativeSpace)
    }
  })
})

function handleOnSearch(options: any) {
  if(options.tab === MENU.MANAGE_COLLABORATIVE_SPACES) {
    isSearchActive.value = true
    collaborativeSpaceSearchStore.search(options.query)
    .then(() => {
      gridViewItems.value = searchedCollaborativeSpaceGridViewItems
      serverItemsLength.value = searchedCSServerItemsLength
    })
  }
}

function handleOnResetSearch() {
  if(isSearchActive.value) {
    isSearchActive.value = false
    gridViewItems.value = fetchedCollaborativeSpaceGridViewItems
    serverItemsLength.value = fetchedCSServerItemsLength
    collaborativeSpaceStore.resetCurrentPage()
    collaborativeSpaceSearchStore.reset()
  }
}

const onSearchBus = useEventBus('onSearch')
const onResetSearchBus = useEventBus('onResetSearch')

onMounted(() => {
  collaborativeSpaceStore.fetch()
  gridViewItems.value = fetchedCollaborativeSpaceGridViewItems
  serverItemsLength.value = fetchedCSServerItemsLength


  onSearchBus.on(handleOnSearch)
  onResetSearchBus.on(handleOnResetSearch)
})

onUnmounted(() => {
  onSearchBus.off(handleOnSearch)
  onResetSearchBus.off(handleOnResetSearch)
})

</script>

<template>
  <div>
    <vu-btn color="primary" class="align-flex-start" @click="showCollaborativeSpaceCreateModal">
      <span class="fonticon fonticon-logo-3dspace-add"></span> {{ createCollabSpaceButtonText }}
    </vu-btn>
    <EmptyView v-if="isSearchActive && (gridViewItems.value?.length === 0)" />
    <vu-scroller v-else>
      <vu-grid-view
        :headers="headers"
        :items="gridViewItems.value"
        :rowsPerPage.sync="rowsPerPage"
        :hideRowsPerPageSelect="true"
        :serverItemsLength="serverItemsLength.value"
        @pageDown="onPageDown"
        @pageUp="onPageUp"
        style="margin-top: 17px;">
        <template v-slot:actions="item">
          <vu-dropdownmenu responsive="true" :items="item.actions">
            <vu-icon class="chevron-menu-icon" icon="chevron-down" />
          </vu-dropdownmenu>
        </template>
      </vu-grid-view>
    </vu-scroller>
    <CollaborativeSpaceModal
      v-model="collaborativeSpaceModel"
      :show="showCollaborativeSpaceModal"
      :modalMode="collaborativeSpaceModalMode"
      @close="closeCollaborativeSpaceModal"
    />
    <ChangeMainOwnerModal
      v-model="collaborativeSpaceModel"
      :show="isChangeOwnerModalVisible"
      @close="closeChangeMainOwnerModal"
    />
  </div>
</template>
