<script setup lang="ts">
import { useTranslations } from '@/composables/generics/translations'
import PeopleSearchInput from './PeopleSearchInput.vue'
import { computed, ref } from 'vue'
import { CollaborativeSpace } from './model/CollaborativeSpace';
import { useCollaborativeSpaceStore } from '@/stores/CollaborativeSpaceStore';
import { message } from 'vuekit';
import { MESSAGE_TIMEOUT } from '@/config/WidgetConfiguration';

const emit = defineEmits<{
  close: []
}>()

defineProps<{
  show: boolean
}>()

const collaborativeSpaceStore = useCollaborativeSpaceStore()

const { $i18n } = useTranslations('manage-collaborative-spaces')
const model = defineModel<CollaborativeSpace>()
const currentMainOwnerName = computed(() => model.value?.owner.name ? model.value.owner.name : '')
const selectedUser = ref('')
const shouldSaveButtonDisabled = computed(() => {
    if(selectedUser.value === '' || selectedUser.value === (model.value?.owner.login ? model.value.owner.login : '')) {
        return true
    }
    return false
})

function changeMainOwner() {
  if(selectedUser.value === '' || selectedUser.value === (model.value?.owner.login ? model.value.owner.login : '')) {
    return
  }

  const collaborativeSpace = {
    name: model.value?.name,
    title: model.value?.title,
    thumbnail: model.value?.thumbnail,
    description: model.value?.description,
    family: {
      name: model.value?.family.name,
      value: model.value?.family.value
    },
    visibility: {
      value: model.value?.visibility.value
    },
    owner:  {
      login: selectedUser.value
    },
    solution: model.value?.solution,
    actions: model.value?.actions.map(action => action),
    contexts: model.value?.contexts.map(context => context),
    created: model.value?.created,
    modified: model.value?.modified
  }

  collaborativeSpaceStore.update(collaborativeSpace)
  .then((updatedCollaborativeSpace: CollaborativeSpace) => {
    emitClose()
    message.create({
      text: $i18n('changeOwnerSuccess', {newMainOwner: updatedCollaborativeSpace.owner.name}),
      color: 'success',
      timeout: MESSAGE_TIMEOUT
    })
  })
  .catch(error => {
    message.create({
      text: $i18n('changeOwnerError'),
      color: 'error',
      timeout: MESSAGE_TIMEOUT
    })
  })
}

function emitClose() {
  emit('close')
}

function userSelected(login: string) {
    selectedUser.value = login
    console.log('-------------------------------------------------------------------------------')
    console.log('login: ' + login)
    console.log('model.value?.owner.login: ' + model.value?.owner.login)
    console.log('-------------------------------------------------------------------------------')
}
</script>
<template>
  <vu-modal
    :show="show"
    :title="$i18n('changeOwnerHeader')"
    :noMobile="true"
    @close="emitClose"
  >
    <template #modal-body>
      <span v-text="$i18n('mainOwner')"></span>
      <PeopleSearchInput
        :current-main-owner-name="currentMainOwnerName"
        @user-selected="userSelected"
      />
    </template>

    <template #modal-footer>
      <vu-btn
        color="primary"
        v-text="$i18n('save')"
        @click="changeMainOwner"
      />
      <vu-btn v-text="$i18n('cancel')" @click="emitClose"/>
    </template>

  </vu-modal>
</template>
