import { rowsPerPage } from "@/config/WidgetConfiguration"
import { axiosClient } from "@/util/AxiosClient"
import { defineStore } from "pinia"
import { ref } from "vue"

export const useCollaborativeSpaceSearchStore = defineStore('collaborativeSpaceSearchStore', () => {

  const COLLABORATIVE_SPACES_API_ENDPOINT = '/resources/enocsmrest/admin/collabspaces/'

  const FETCH_API_LIMIT = rowsPerPage

  const collaborativeSpaces = ref()

  let totalPages: number, lastFetchedPageNumber: number

  const currentPage = ref(0)

  const serverItemsLength = ref(0)

  const query = ref()

  function search(userQuery: string) {
    query.value = userQuery
    return new Promise((resolve, reject) => {
      search_Internal(0)
      .then((response: any) => {
        collaborativeSpaces.value = response.data.items
        totalPages = response.data.nbPages
        lastFetchedPageNumber = 0
        updateServerItemsLength(response)
        resolve(response)
      })
      .catch((error) => reject(error))
    })
  }

  function search_Internal(page: number) {
    return new Promise((resolve, reject) => {
      axiosClient.get(COLLABORATIVE_SPACES_API_ENDPOINT, {
        params: {filter: query.value, page: page, limit: FETCH_API_LIMIT}
      })
      .then(response => {
        resolve(response)
      })
      .catch(error => reject(error))
    })
  }

  function next() {
    if(!shouldSearchNext()) {
      return
    }

    let nextPageNumber = lastFetchedPageNumber + 1
    return new Promise((resolve, reject) => {
      search_Internal(nextPageNumber)
      .then((response: any) => {
        collaborativeSpaces.value = collaborativeSpaces.value.concat(response.data.items)
        totalPages = response.data.nbPages
        lastFetchedPageNumber = nextPageNumber
        updateServerItemsLength(response)
        currentPage.value = currentPage.value + 1
        resolve(response)
      })
      .catch(error => reject(error))
    })
  }

  function previous() {
    currentPage.value = currentPage.value - 1
  }

  function reset() {
    collaborativeSpaces.value = undefined
    totalPages = 0
    lastFetchedPageNumber = 0
    currentPage.value = 0
    serverItemsLength.value = 0
    query.value = undefined
  }

  function shouldSearchNext() {
    return currentPage.value === lastFetchedPageNumber
  }

  function updateServerItemsLength(response: any) {
    if((lastFetchedPageNumber + 1) === totalPages) {
      serverItemsLength.value = ((totalPages - 1) * FETCH_API_LIMIT) + response.data.items.length
    } else {
      serverItemsLength.value = totalPages * FETCH_API_LIMIT
    }
  }

  return { collaborativeSpaces, serverItemsLength, search, next, previous, reset }
})
