<script setup lang="ts">
import { computed } from 'vue'
import { useTranslations } from '@/composables/generics/translations';
import { rowsPerPage } from '@/config/WidgetConfiguration';

const emit = defineEmits<{
  close: []
}>()

defineProps<{
  show: boolean
}>()

const model = defineModel()

const { $i18n } = useTranslations('manage-permissions')

const headers = computed(() => {
  return [
  {
      property: 'title',
      label: $i18n('title'),
      sortable: false
    },
    {
      property: 'name',
      label: $i18n('name'),
      sortable: false
    },
    {
      property: 'company',
      label: $i18n('company'),
      sortable: false
    },
    {
      property: 'role',
      label: $i18n('role'),
      sortable: false
    }
  ]
})

function emitClose() {
  emit('close')
}

</script>

<template>
  <vu-modal :show="show" :title="$i18n('credentialsList')" :noMobile="true" @close="emitClose">
    <template #modal-body>
      <vu-grid-view :headers="headers" :items="model" :rowsPerPage.sync="rowsPerPage" :hideRowsPerPageSelect="true"/>
    </template>
    <template #modal-footer>
      <vu-btn v-text="$i18n('close')" @click="emitClose" />
    </template>
  </vu-modal>
</template>
<style scoped>
:deep(.modal-wrap) {
    width: 800px;
}
</style>
