import { defineStore } from "pinia"
import { Widget_Preference } from "@/typings/Widget"
import { requirejs } from "@/modules/require"
import { i3DXCompassServices_AMD } from "@/typings/i3DXCompassServices"
import { ref } from "vue"
import { axiosClient } from "@/util/AxiosClient"

interface PreferencesOption {
  label: string,
  value: string
}

export const usePreferencesStore = defineStore('preferencesStore', () => {
  let x3DSpacePlatforms: any[] = []

  const selectedPlatform = ref(null)

  const isPreferencesLoading = ref<boolean>(false)
  const isPreferencesLoaded = ref<boolean>(false)
  const preferencesLoadingError = ref<string | null>(null)

  function initializeWidgetPreferences() {
    setDefaultPreferences()
    fetch3DSpacePlatforms()
  }

  function setDefaultPreferences() {
    const defaultPreferences: Widget_Preference = {
      type : 'list',
      name : 'collab-storage',
      label : '3DEXPERIENCE Platform',
      options : [{
        label : 'Loading',
        value : ''
      }],
    }
  
    window.widget.addPreference(defaultPreferences)  
  }

  async function fetch3DSpacePlatforms() {
    isPreferencesLoading.value = true
    isPreferencesLoaded.value = false
    preferencesLoadingError.value = null
    
    console.log('------------------------- fetch3DSpacePlatforms start isPreferencesLoading: ' + isPreferencesLoading.value)
    console.log('------------------------- fetch3DSpacePlatforms start isPreferencesLoaded: ' + isPreferencesLoaded.value)
    console.log('------------------------- fetch3DSpacePlatforms start preferencesLoadingError: ' + preferencesLoadingError.value)
    const [ i3DXCompassServices ] = await requirejs<[i3DXCompassServices_AMD]>(['DS/i3DXCompassServices/i3DXCompassServices'])
    i3DXCompassServices.getPlatformServices({onComplete, onFailure})
    console.log('------------------------- fetch3DSpacePlatforms end')
  }

  function onComplete(platforms: any[]) {
    console.log('------------------------- onComplete start')
    try {
      let platformToBeSet
      x3DSpacePlatforms = filter3DSpacePlatforms(platforms)
      if(x3DSpacePlatforms.length > 0) {
        const widgetCollabStorageId = window.widget.getValue('collab-storage')
    
        if(widgetCollabStorageId === '') {
          const x3dPlatformId = window.widget.getValue('x3dPlatformId')
    
          if(x3dPlatformId) {
            platformToBeSet = getPlatformById(x3dPlatformId)
            
            if(platformToBeSet) {
              window.widget.setValue('collab-storage', platformToBeSet.platformId)
              window.widget.setValue('tenantAware', true)
            }
          } else {
            platformToBeSet = getPlatformByIndex(0)
          }
        } else {
          platformToBeSet = getPlatformById(widgetCollabStorageId)
        }
      }
      
      if(platformToBeSet) {
        updateAxiosConfiguraiton(platformToBeSet)
        setPreferences(platformToBeSet)
        selectedPlatform.value = platformToBeSet
      } else {
        preferencesLoadingError.value = 'UndefinedStorage'
      }
    } catch( err: any ) {
      preferencesLoadingError.value = err.message || 'Failed to get platform services'
    }
    finally {
      isPreferencesLoading.value = false
      isPreferencesLoaded.value = true
    }
    console.log('------------------------- onComplete end')
    console.log('------------------------- onComplete end isPreferencesLoading: ' + isPreferencesLoading.value)
    console.log('------------------------- onComplete end isPreferencesLoaded: ' + isPreferencesLoaded.value)
    console.log('------------------------- onComplete end preferencesLoadingError: ' + preferencesLoadingError.value)
  }

  function filter3DSpacePlatforms(platforms: any[]) {
    const filteredPlatforms: any[] = []
    platforms.filter(function(platform) {
      if(platform['3DSpace']) {
        filteredPlatforms.push({
          platformId: platform.platformId,
          name: platform.displayName,
          url: platform['3DSpace']
        })
      }
    })

    return filteredPlatforms
  }

  function getPlatformById(platformId: any) {
    return x3DSpacePlatforms.find(function(platform) {
      if(platformId === platform.platformId) {
        return platform
      }
    })
  }

  function getPlatformByIndex(index: number) {
    return x3DSpacePlatforms[index];
  }

  function onFailure(error: any) {
    isPreferencesLoading.value = false
    isPreferencesLoaded.value = true
    preferencesLoadingError.value = error.message
    console.log(error)
  }

  function setPreferences(platformToBeSet: any) {
    const preferences: Widget_Preference = {
      type : 'list',
      name : 'collab-storage',
      label : '3DEXPERIENCE Platform',
      options : []
    }

    preferences.options = x3DSpacePlatforms.map(function(platform) {
      return {
        label: platform.name, // TODO unescapeHTML()
        value: platform.platformId
      } as PreferencesOption
    })

    if(x3DSpacePlatforms.length === 1 && platformToBeSet) {
      preferences.type = 'hidden'
    }

    window.widget.addPreference(preferences)

    // TODO Editing preferences

    window.widget.setValue('x3dPlatformId', platformToBeSet.platformId)

    // TODO Platform API subscribe
  }

  function updateAxiosConfiguraiton(platformToBeSet: any) {
    axiosClient.defaults.baseURL = platformToBeSet.url
    axiosClient.defaults.params = {
      tenant: platformToBeSet.platformId
    }
  }

  return { isPreferencesLoading, isPreferencesLoaded, preferencesLoadingError, selectedPlatform, initializeWidgetPreferences }
})
