<script setup lang="ts">
import { onMounted, onBeforeUnmount, ref, watch } from 'vue';
import { CollaborativeSpace } from './model/CollaborativeSpace';
import { requirejs } from '@/modules/require';
import { UWAElement_AMD } from '@/typings/UWAElement';
import { usePreferencesStore } from '@/stores/PreferencesStore';
import { storeToRefs } from 'pinia';
import { useSessionStore } from '@/stores/SessionStore';
import { useUserStore } from '@/stores/UserStore';
import { I3DXCompassPlatformServices_AMD } from '@/typings/i3DXCompassPlatformServices';
import { create } from '@/stores/MembersStore';
import { useManageMembersStore } from '@/stores/ManageMembersStore';

const props = defineProps(['modelValue']);

const containerRef = ref(null);
const controllerRef = ref(null);

const preferencesStore = usePreferencesStore()
const { selectedPlatform } = storeToRefs(preferencesStore)

const sessionStore = useSessionStore()
const { session } = storeToRefs(sessionStore)

const userStore = useUserStore()
const { user } = storeToRefs(userStore)

const manageMembersStore = useManageMembersStore()
const { isManageMembersVisible } = storeToRefs(manageMembersStore)

function closeManageMembers() {
  console.log('-------------------------------closeManageMembers')
  isManageMembersVisible.value = false
}

// Utility to clean up controller
const cleanupController = () => {
  console.log('-------------------------------cleanupController')

  if (controllerRef.value) {
    console.log('-------------------------------if(controllerRef.value)')
    controllerRef.value.destroy?.();
    controllerRef.value = null;
  }

  console.log('-------------------------------cleanupController done')
};

// Load controller
const loadController = () => {
  console.log('-------------------------------loadController')
  cleanupController();

  if (!containerRef.value) return;

  requirejs<[any, UWAElement_AMD]>([
        'DS/i3DXPnOMultiOrg/OrgOperatorController',
        'UWA/Element'
    ]).then(([OrgOperatorController, UWAElement]) => {
    const tenantId = selectedPlatform.value?.platformId
    const currentUser = user.value?.id
    const collabSpace = props.modelValue.name
    const collabSpaceTitle = props.modelValue.title
    const securityContext = getNominalContexts(props.modelValue.contexts)
    const owner = isOwner(props.modelValue.contexts).isOwner
    const ownerRestricted = isOwner(props.modelValue.contexts).isOwnerRestricted
    const mainOwner = props.modelValue.owner.login
    const availableRoles = session.value?.availableRoles

    const orgOperatorController = new OrgOperatorController({
        container: containerRef.value,
        tenantId: tenantId,
        currentUser: currentUser,
        collabSpace: collabSpace,
        collabSpaceTitle: collabSpaceTitle,
        securityContext: securityContext,
        owner: owner,
        ownerRestricted: ownerRestricted,
        mainOwner: mainOwner,
        roles: availableRoles,
        onAddUsers: async function () {
          requirejs<[any]>([
            'DS/W3DSpaceComponents/Views/shareCollaborativeSpace'
          ]).then (([shareCollaborativeSpace]) => {
          return createInode()
          .then(response => {
            shareCollaborativeSpace(response)
          })
        })
        },
        onCloseView: function () {
          /*document.querySelector('.multiOrg').destroy()
          window.widget.removeEvents({
            onLoad: orgOperatorController.load.bind(orgOperatorController),
            onRefresh: orgOperatorController.refresh.bind(orgOperatorController),
            onDestroy: orgOperatorController.destroy.bind(orgOperatorController)
          })
          
          orgOperatorController.destroy()
          window.orgOperatorController = null*/
          
          closeManageMembers()
        }
    })
    /*window.orgOperatorController = orgOperatorController
    window.widget.addEvents({
      onLoad: orgOperatorController.load.bind(orgOperatorController),
      onRefresh: orgOperatorController.refresh.bind(orgOperatorController),
      onDestroy: orgOperatorController.destroy.bind(orgOperatorController)
    })*/
    orgOperatorController.load()
    controllerRef.value = orgOperatorController;
    })
    console.log('-------------------------------loadController done')
};

function createInode() {
  const platformId = selectedPlatform.value?.platformId
  
  return new Promise(async (resolve, reject) => {
    const [i3DXCompassPlatformServices] = await requirejs<[I3DXCompassPlatformServices_AMD]>(['DS/i3DXCompassPlatformServices/i3DXCompassPlatformServices'])
    i3DXCompassPlatformServices.getPlatformServices({
      platformId: platformId,
      onComplete: function (response: any) {
        const inode = {
          id: props.modelValue.name,
          title: props.modelValue.title,
          actions: props.modelValue.actions,
          owners: props.modelValue.owner,
          contexts: props.modelValue.contexts,
          _auth: {
            platform: {
              displayName: response.displayName,
              id: platformId,
              url: response['3DDrive'],
              url3DSpace: response['3DSpace'],
              urlDashboard: response['3DDashboard'],
              urlDfcs: response.distributedfilestore,
              urlDrivesHub: response['driveshub'],
              urlPassport: response['3DPassport'],
              urlSwym: response['3DSwym'],
              urlUsersGroup: response['usersgroup'],
              urls3DSearch: response['3DSearch']
            },
            platformId: platformId,
            getStorage: function() {
              return this.platform
            }
          },
          drive: {
            //id: '', // TODO How to get correct id of OnPremise
            _attributes: {
              availableRoles: session.value?.availableRoles,
              user: user.value
            }
          },
          permissions: {
            create: function (attributes, options) {
              create(attributes)
              .then(response => {
                var tempModel = {
                  collabspace: props.modelValue.name,
                  get: function (key) {
                    return this[key]
                  }
                }
                options.onComplete(tempModel, response)
              })
              .catch(error => {
                console.log('Error: ' + error)
                options.onFailure(error)
              })
            }
          },
          get: function (key) {
            return this[key]
          }
        }
        i3DXCompassPlatformServices.getPlatformConfig({
          key: 'share',
          platformId: platformId,
          onComplete: function (respConfig) {
            inode.drive._attributes.shareWithinvitation = respConfig
            resolve(inode)            
          }
        })
      },
      onFailure: function (error) {
        console.log(error)
        reject(error)
      }
    })
  })
}

function getNominalContexts (contexts: any[]) {
  var i, context

  if (contexts.length > 1) {
    for (i = 0; i < contexts.length; i++) {
      context = contexts[i]
      if (context.role === 'VPLMProjectAdministrator') {
        break
      }
      if (context.role === '3DSRestrictedOwner') {
        break
      }
    }
    return context.name
  } else {
    // For newly created CS in Collaborative Spaces Control Center,
    // the REST API returns Security Context in an object format
    // (instead of regular String). Becuase of this below check is added
    // IR-1208950-3DEXPERIENCER2024x
    if(typeof contexts[0] === 'object') {
      return contexts[0].name
    } else {
      return contexts
    }
  }
}

function isOwner (contexts: any[]) {
  var context
  var isOwner, isOwnerRestricted
  isOwner = true
  return { isOwner: isOwner, isOwnerRestricted: isOwnerRestricted }
}

watch(() => props.modelValue, (newVal) => {
  if (newVal) {
    console.log('-------------------------------inside if(newVal)')
    loadController();
  } else {
    console.log('-------------------------------else (newVal)')
    cleanupController();
  }
});

onMounted(() => {
  console.log('-------------------------------onMounted')
  console.log('modelValue: ' + props.modelValue)
  if (props.modelValue) {
    console.log('-------------------------------onMounted->if(props.modelValue)')
    loadController();
  }
})

onBeforeUnmount(() => {
  console.log('-------------------------------onBeforeUnmount')
  cleanupController();
});

</script>
<template>
  <div ref="containerRef" class="multiOrg multiOrg--visible"></div>
</template>
