import { DS_LANG } from "@/config/WidgetConfiguration";

export function getDate(date: any) {
  const newDate = new Date(Number(date) * 1000)
  const dateStr = newDate.toLocaleString(DS_LANG, {
    year: 'numeric', month: 'short', day: '2-digit'
  })

  return dateStr
}

export function getDateAndTime (date: any) {
  const newDate = new Date(Number(date) * 1000);
  const timeStr = newDate.toLocaleString(DS_LANG, {
      hour: '2-digit', minute: '2-digit', second: '2-digit', hourCycle: 'h23'
  })

  return getDate(date) + ' - ' + timeStr
}
