import { CollaborativeSpace } from "@/components/model/CollaborativeSpace"
import { rowsPerPage } from "@/config/WidgetConfiguration"
import { axiosClient } from "@/util/AxiosClient"
import { defineStore } from "pinia"
import { ref } from "vue"

export const useCollaborativeSpaceStore = defineStore('collaborativeSpaceStore', () => {
  const COLLABORATIVE_SPACES_API_ENDPOINT = '/resources/enocsmrest/admin/collabspaces/'

  const FETCH_API_LIMIT = rowsPerPage

  const collaborativeSpaces = ref()

  let totalPages: number, lastFetchedPageNumber: number

  const currentPage = ref(0)

  const serverItemsLength = ref(0)

  function create(requestBody: any) {
    return new Promise((resolve, reject) => {
      axiosClient.post(COLLABORATIVE_SPACES_API_ENDPOINT, requestBody)
      .then(response => {
        collaborativeSpaces.value.unshift(response.data)
        updateServerItemsLengthAfterCreate()
        resolve(response.data)
      })
      .catch(error => reject(error))
    })
  }

  function fetch() {
    fetch_Internal(0)
    .then((response: any) => {
      collaborativeSpaces.value = response.data.items
      totalPages = response.data.nbPages
      lastFetchedPageNumber = 0
      updateServerItemsLengthAfterFetch(response)
    })
  }

  function fetch_Internal(page: number) {
    return new Promise((resolve, reject) => {
      axiosClient.get(COLLABORATIVE_SPACES_API_ENDPOINT, {
        params: {page: page, limit: FETCH_API_LIMIT, withactions: true}
      })
      .then(response => {
        resolve(response)
      })
      .catch(error => reject(error))
    })
  }

  function next() {
    if(!shouldFetchNext()) {
      return
    }

    let nextPageNumber = lastFetchedPageNumber + 1
    fetch_Internal(nextPageNumber)
    .then((response: any) => {
      collaborativeSpaces.value = collaborativeSpaces.value.concat(response.data.items)
      totalPages = response.data.nbPages
      lastFetchedPageNumber = nextPageNumber
      updateServerItemsLengthAfterFetch(response)
      currentPage.value = currentPage.value + 1
    })
  }

  function previous() {
    currentPage.value = currentPage.value - 1
  }

  function resetCurrentPage() {
    currentPage.value = 0
  }

  function shouldFetchNext() {
    return currentPage.value === lastFetchedPageNumber
  }

  function update(requestBody: any): Promise<CollaborativeSpace> {
    return new Promise((resolve, reject) => {
      axiosClient.put(COLLABORATIVE_SPACES_API_ENDPOINT + requestBody.name, requestBody)
      .then(response => {
        collaborativeSpaces.value.find((collaborativeSpace: any, index: number) => {
          if(collaborativeSpace.name === response.data.name) {
            collaborativeSpaces.value[index] = response.data
            return
          }
        })
        resolve(response.data)
      })
      .catch(error => reject(error))
    })
  }

  function updateServerItemsLengthAfterCreate() {
    if(serverItemsLength.value % FETCH_API_LIMIT === 0) {
      totalPages = totalPages + 1
      lastFetchedPageNumber = lastFetchedPageNumber + 1
    }
    
    serverItemsLength.value = serverItemsLength.value + 1
  }

  function updateServerItemsLengthAfterFetch(response: any) {
    if((lastFetchedPageNumber + 1) === totalPages) {
      serverItemsLength.value = ((totalPages - 1) * FETCH_API_LIMIT) + response.data.items.length
    } else {
      serverItemsLength.value = totalPages * FETCH_API_LIMIT
    }
  }

  return { collaborativeSpaces, serverItemsLength, create, fetch, next, previous, resetCurrentPage, update }
})
