<script setup lang="ts">
import { computed, onMounted, reactive, ref } from 'vue'
import { useTranslations } from '../composables/generics/translations'
import { MENU } from '../constants/menuConstants.ts'

const emit = defineEmits<{
  menuChange: [menuName: string]
}>()

const { $i18n, promise: translationsPromise } = useTranslations('main-menu')
const manageCollabSpacesTabText = computed(() => $i18n(MENU.MANAGE_COLLABORATIVE_SPACES))
const managePermissionsTabText = computed(() => $i18n(MENU.MANAGE_PERMISSIONS))
const manageSettingsTabText = computed(() => $i18n(MENU.MANAGE_SETTINGS))

const items = computed(() => reactive([
  {
    name: MENU.MANAGE_COLLABORATIVE_SPACES,
    text: manageCollabSpacesTabText.value,
    icon: 'doc-text'
  },
  {
    name: MENU.MANAGE_PERMISSIONS,
    text: managePermissionsTabText.value,
    icon: 'users'
  },
  {
    name: MENU.MANAGE_SETTINGS,
    text: manageSettingsTabText.value,
    icon: 'cog'
  }
]))

const item = ref()

function updateModelValue(item: any) {
  emit('menuChange', item.name)
}

onMounted(() => {
  translationsPromise.then(() => {
    item.value = items.value[0]
  })
})

</script>

<template>
  <vu-facets-bar
    v-model="item"
    :items="items"
    @update:modelValue="updateModelValue"
  />
</template>

<style scoped>
.vu-facets-bar {
    margin-top: 10px;
}
</style>
