import VueKit from 'vuekit'
import '@ds/vuekit/dist/VUEKIT.css'
import './global.css'

import { createApp } from "vue"
import { createPinia } from "pinia"
import App from "./App.vue"

console.log('------------------------- Main script loaded')

const pinia = createPinia()

const app = createApp(App)
app.use(VueKit, {disableTooltipsOnDevices: true, globalRegister: true})
app.use(pinia)

window.widget.addEvent('onLoad', function(event: any) {
  console.log('------------------------- Widget onLoad start: ' + event)
})

window.widget.addEvent('onRefresh', function() {
  console.log('------------------------- Widget onRefresh')
  window.location.reload()
})

window.widget.addEvent('onEdit', function(event: any) {
  console.log('------------------------- Widget onEdit: ' + event)
})

window.widget.addEvent('endEdit', function(event: any) {
  console.log('------------------------- Widget endEdit: ' + event)
})

app.mount('#app')
