import { axiosClient } from '@/util/AxiosClient'
import { request } from 'http'
import { defineStore } from 'pinia'
import { ref } from 'vue'

export const useAdminConfigStore = defineStore('adminConfigStore', () => {

  const CONFIG_API_ENDPOINT = '/resources/enocsmrest/admin/config'

  const adminConfigData = ref(null)

  function fetch() {
    return new Promise((resolve, reject) => {
      axiosClient.get(CONFIG_API_ENDPOINT)
      .then(response => {
        adminConfigData.value = response.data
        resolve(response.data)
      })
      .catch(error => reject(error))
    })
  }

  function update(requestBody: any) {
    return new Promise((resolve, reject) => {
      axiosClient.put(CONFIG_API_ENDPOINT, requestBody)
      .then(response => {
        adminConfigData.value = response.data
        resolve(response.data)
      })
      .catch(error => reject(error))
    })
  }

  return { adminConfigData, fetch, update }
})
